@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  font-family: "Noto Sans", sans-serif;
}

body {
  background-color: lightgray;
  color: white;
}

.notification-link {
  position: relative;
  color: red;
  margin-left: 2rem;
  cursor: pointer;
}

.alert-active {
  position: absolute;
  background-color: red;
  width: 10px;
  z-index: 3;
  height: 10px;
  border-radius: 50%;
  -webkit-transform: translate(115px, -20px);
          transform: translate(115px, -20px);
}

.panel {
  position: absolute;
  width: 200px;
  height: 0px;
  cursor: pointer;
  overflow: hidden;
  background-color: black;
  padding: 0.5rem;
  opacity: 0;
}
.panel ul {
  list-style: none;
  padding: 0;
  width: 200px;
  height: 150px;
  overflow-y: scroll;
}
.panel li {
  text-align: center;
  height: 2rem;
  padding-top: 0.5rem;
  border-bottom: 1px solid gray;
}

.panel-open {
  position: absolute;
  background-color: black;
  min-height: 150px;
  border-radius: 10px;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
  opacity: 1;
  transition: all 0.2s ease-in-out;
}

.panel-close {
  position: absolute;
  width: 200px;
  height: 0px;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}

.page-container {
  width: 95vw;
  margin: auto;
  padding-top: 70px;

  overflow: hidden;
}

.header-format {
  min-height: 200px;
  color: black;
  background: white;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 1.5rem;
  font-weight: bolder;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.div-content {
  display: flex;
  padding: 2rem 0 2rem 0;
  background-color: gray;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.footer-format {
  min-height: 200px;
  background-color: black;
  padding: 2rem;
  position: relative;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}

.card-container {
  background-color: white;
  color: black;
  border: 1px solid lightgray;
  padding: 1rem;
  overflow: hidden;
}

.card-head {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.card-head div[id="user-image"] {
  width: 60px;
}

div[id="user-image"] img {
  width: 60px;
  margin: auto;
  border-radius: 50%;
}

.card-content {
  min-height: 70px;
  padding: 0.5rem;
}

.card-content p {
  margin-top: 0.5rem;
  text-align: justify;
  text-justify: inter-word;
}

.card-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: lightgray;
  padding: 0.3rem 1rem 0.3rem 1rem;
  border-radius: 5px;
}

.likes-section {
  display: flex;
  align-items: center;
}

/* skeletal */

.fill-bar {
  height: 10px;
  width: 400px;
  background-color: lightgray;
  margin-bottom: 1rem;
}

.short {
  width: 200px;
}


.button {
  background: none;
  border: none;
}

.button .fa-heart {
  font-size: 1.4rem;
}

.btn-profile {
  background-color: black;
  padding: 0.5rem 1rem 0.5rem 1rem;
  font-size: 1rem;
  color: white;
  border-radius: 20px;
  transition: all 0.2s ease-in-out;
}

.btn-profile:hover {
  background-color: lightgray;
  color: black;
}

/* without user */
.feed-head-no-user {
  background-color: black;
  padding: 0.5rem;
  text-align: center;
}

.feed-head-no-user * {
  text-decoration: none;
  color: white;
}

/* with user */
.feed-head-container {
  background-color: black;
  border-radius: 5px;
}

.feed-head-header {
  padding: 1rem;
  display: flex;
  align-items: center;
}

.feed-head-header img {
  border-radius: 50%;
  margin-right: 1rem;
}

.feed-form {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  text-align: center;
  align-items: center;
}

.form-input {
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.feed-form input {
  height: 30px;
  font-size: 1rem;
  border: none;
  background-color: lightgray;
  padding-left: 0.5rem;
}

.feed-form textarea {
  border: none;
  padding-left: 0.5rem;
}

.feed-form > * {
  width: 100%;
}

.footer-panel-head {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-panel-head .fa-hamsa {
  margin: 0.5rem 0 0.5rem 0;
  font-size: 2rem;
}

.footer-panel-head .submit-button {
  width: 150px;
  height: 30px;
  background-color: aquamarine;
  border: none;
  border-radius: 20px;
  font-size: 1rem;
  transition: all 0.2s ease-in-out;
}

.footer-panel-head .submit-button:hover {
  background-color: rgb(237, 42, 255);
}

.form-format {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  background-color: black;
  width: 50%;
  text-align: center;
  height: 300px;
  border-radius: 10px;
  align-items: center;
}

.form-title {
  margin-bottom: 1rem;
  font-size: 2rem;
}

.input-format {
  width: 300px;
  border: none;
  background: lightgray;
  padding: 0.7rem;
  border-radius: 5px;
  margin: 0.25rem;
}

.buttons-div {
  margin: 0.5rem;
}

.button-format {
  padding: 0.7rem;
  min-width: 150px;
  border: none;
  border-radius: 5px;
  background-color: aquamarine;
  font-size: 1rem;
}

.profileuser-container {
  background-color: black;
  border-radius: 10px;
  width: 300px;
  min-height: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-head {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}
.profile-head div img {
  border-radius: 50%;
}

.profile-head button {
  background: none;
  border: none;
  font-size: 1rem;
  color: white;
}

.profile-head button:hover {
  color: aquamarine;
}
.profile-body {
  position: relative;
  background-color: aquamarine;
  width: 250px;
  min-height: 200px;
  border-radius: 5px;
  padding: 1rem;
  text-align: center;
  color: black;
}

.body-form {
  text-align: left;
  margin-top: 2rem;
  min-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.body-form > * {
  width: 100%;
}

.body-form > p {
  margin: 0.5rem 0 0.5rem 0;
}

.body-form > input {
  font-size: 0.9rem;
  padding-left: 0.5rem;
}

.buttons-bar {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: space-around;
  bottom: 0;
  padding-bottom: 1rem;
  left: 0;
}

